import React, { useCallback, useState, useMemo } from "react";
import {
  Container,
  Sidebar,
  Logo,
  AddFeatureButton,
  SearchBar,
  Content,
  SectionContent,
  SectionTitle,
  Section,
  UserInfo,
  FormContainer,
  InputContainer,
  StyledInput,
  SubmitButton,
  AddExamButton,
} from "@pages/Main/styles";

import useSWR from "swr";
import fetcher from "@utils/fetcher";
import useInput from "hooks/useInput";
import axios from "axios";
import ExamPaperList from "@components/ExamPaperList";
import ExamTypeList from "@components/ExamTypeList";
import { useNavigate } from "react-router";

const Main = () => {
  const { data, error, mutate } = useSWR("/api/user", fetcher);
  const [showInput, setShowInput] = useState(false);
  const [newTypeName, onChangeNewTypeName, setNewTypeName] = useInput("");
  const navigate = useNavigate();
  const examTypeList = useMemo(() => data?.ExamTypeLists, [data]); // useMemeo?
  const onClickAddNewType = useCallback(() => {
    setShowInput(!showInput);
  }, [showInput]);
  const onSubmitNewType = useCallback(
    async (e) => {
      e.preventDefault();
      try {
        const newExamType = await axios.post("/api/main/createExamType", {
          newTypeName,
        });
        setNewTypeName("");
      } catch (error) {
        console.error(error);
      }
    },
    [newTypeName, data]
  );
  const onClickLogout = useCallback(async () => {
    await axios.post("/api/user/logout").then((response) => {
      console.log(response.data);
      mutate(false);
    });
  }, []);
  if (data === false) {
    navigate("/login");
  }
  return (
    <Container>
      <Sidebar>
        <Logo>
          {/* 임시 이미지 */}
          <img src="https://via.placeholder.com/30" alt="Logo" />
          <span>{data?.nickname}</span>
          <button onClick={onClickLogout}>Logout</button>
        </Logo>
        <AddFeatureButton>
          {/* 임시 아이콘 */}
          <span>Exam</span>
        </AddFeatureButton>
      </Sidebar>
      <Content>
        <SearchBar>
          <input type="text" placeholder="Search" />
        </SearchBar>
        <UserInfo>
          <div className="notifications"></div>
          <div className="user-name">Peter Parker</div>
          <div className="user-name">Exam</div>
        </UserInfo>

        <AddExamButton onClick={onClickAddNewType}>
          {showInput ? "Close" : "Add New Type"}
        </AddExamButton>
        <InputContainer>
          <form onSubmit={onSubmitNewType}>
            <FormContainer>
              <InputContainer>
                <StyledInput
                  value={newTypeName}
                  onChange={onChangeNewTypeName}
                  type="text"
                  placeholder="Type"
                  show={showInput}
                />
                <SubmitButton type="submit" show={showInput}>
                  Submit
                </SubmitButton>
              </InputContainer>
            </FormContainer>
          </form>
        </InputContainer>
        {examTypeList?.length > 0 ? (
          <ExamTypeList examTypeList={examTypeList} />
        ) : (
          <div>no data</div>
        )}
      </Content>
    </Container>
  );
};
export default Main;
